import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';
import { AuthData } from './auth-data.model';
import { environment } from 'src/environments/environment';

@Injectable( {providedIn: "root"})
export class AuthService {
    private isAuthenticated =false;
    private token: string;
    private userId: string;
    private tokenTimer: any;
    private authStatusListener = new Subject<boolean>();

    constructor(private http: HttpClient, private router: Router) {}

    getToken(){
        return this.token;
    }

    getIsAuth(){
       return this.isAuthenticated;
    }

    getUserId(){
        return this.userId;
    }

    getAuthStatusListener(){
        return this.authStatusListener.asObservable();
    }
    
    createUser(email: string, password: string){
        const authData: AuthData = {email: email, password: password}
        this.http.post( environment.apiUrl + "/user/signup", authData)
            .subscribe(response => {
                    this.router.navigate(['/']);
            }, error => {
                this.authStatusListener.next(false);
            });
    }

    login(email: string, password: string){
        const authData: AuthData = {email: email, password: password}
        this.http.post<{token: string, expiresIn: number, userId: string}>( environment.apiUrl + "/user/login", authData)
        .subscribe(response => {
            const token = response.token;
            this.token = token;
            if (token) {
                const expiresInDuration = response.expiresIn;
                this.setAuthTimer(expiresInDuration);
                this.isAuthenticated = true;
                this.userId = response.userId;
                this.authStatusListener.next(true);
                const now = new Date();
                const expirationData = new Date(now.getTime() + expiresInDuration * 1000);
                this.saveAuthData(token, expirationData, this.userId);
                this.router.navigate(["/"]);
            }
        }, error => {
            this.authStatusListener.next(false);
        });
    };

    autoAuthUser(){
        const authInformation = this.getAuthData();
        if (!authInformation){
            return;
        }
        const now = new Date();
        const expiresIn = authInformation.expirationData.getTime() - now.getTime();
        if (expiresIn > 0){
            this.token = authInformation.token;
            this.isAuthenticated = true;
            this.userId = authInformation.userId;
            this.setAuthTimer(expiresIn / 1000);
            this.authStatusListener.next(true);
        }
    }

    logout(){
        this.token = null;
        this.isAuthenticated = false;
        this.authStatusListener.next(false);
        clearTimeout(this.tokenTimer);
        this.clearAuthData();
        this.userId = null;
        this.router.navigate(["/"]);
    }

    private saveAuthData(token: string, expirationData: Date, userId: string) {
        localStorage.setItem("token", token);
        localStorage.setItem("expiration", expirationData.toISOString());
        localStorage.setItem("userId", userId);
    }

    private clearAuthData(){
        localStorage.removeItem("token");
        localStorage.removeItem("expiration");
        localStorage.removeItem("userId");
    }

    private getAuthData(){
        const token = localStorage.getItem("token");
        const expirationData = localStorage.getItem("expiration");
        const userId = localStorage.getItem("userId");
        if (!token || !expirationData) {
            return;
        }
        return {
            token: token,
            expirationData: new Date(expirationData),
            userId: userId
        }
    }

    private setAuthTimer(duration: number){
        this.tokenTimer = setTimeout(() => {
            this.logout()
        }, duration * 1000);
    }
}